
import { defineComponent, onMounted, ref } from "vue"
import { NavBar, Toast } from "vant"
import MessageListCard from "./MessageListCard.vue"
import MessageService from "@/services/message"

export default defineComponent({
  name: "Message",
  components: {
    MessageListCard,
    [NavBar.name]: NavBar,
    [Toast.name]: Toast,
  },
  setup() {
    let feedbackTitle = ref<string>("")
    let orderTitle = ref<string>("")
    let noticeTitle = ref<string>("")

    function getMessage() {
      MessageService.getMessage().then(({ status, data, message }) => {
        if (status !== 200) {
          Toast(message)
          return
        }
        if (data.feedback?.question !== undefined) {
          feedbackTitle.value = data.feedback?.question
        }
        if (data.message?.title !== undefined) {
          noticeTitle.value = data.message?.title
        }
        if (data.orderNotice?.updatetime !== undefined) {
          orderTitle.value = data.orderNotice.updatetime
        }
      })
    }
    onMounted(getMessage)

    return { feedbackTitle, orderTitle, noticeTitle }
  },
})
