<template>
  <div class="message-list-card" @click="$emit('click')">
    <div class="message-list-card__img">
      <van-icon class="message-list-card__icon" :name="icon" size="32" :style="iconStyle" />
    </div>
    <div class="message-list-card__content">
      <div class="message-list-card__content__title">
        <div class="message-list-card__content__title__on">{{ titleOn }}</div>
        <div class="message-list-card__content__title__time">{{ titleTime }}</div>
      </div>
      <div class="message-list-card__content__value">{{ value }}</div>
    </div>
  </div>
</template>

<script lang="ts">
import { computed, CSSProperties, defineComponent } from "vue"
import { Icon, Image as VanImage } from "vant"

export default defineComponent({
  name: "MessageListCard",
  components: {
    [VanImage.name]: VanImage,
    [Icon.name]: Icon,
  },
  props: {
    titleOn: String,
    titleTime: String,
    icon: String,
    iconBackground: String,
    value: String,
  },
  setup(props) {
    const iconStyle = computed(() => {
      const style: CSSProperties = {}
      if (props.iconBackground) {
        style.background = props.iconBackground
      }
      return style
    })
    return { iconStyle }
  },
})
</script>
