<template>
  <div class="message-list">
    <van-nav-bar title="消息中心" left-text="" left-arrow @click-left="$router.back()" />
    <message-list-card
      icon="comment"
      titleOn="平台公告"
      titleTime="2021-05-05 18:18:18"
      :value="noticeTitle"
      icon-background="#f7c15d"
      @click="$router.push('/notices')"
    />
    <message-list-card
      icon="balance-list"
      titleOn="订单通知"
      titleTime="2021-05-05 18:18:18"
      :value="orderTitle"
      icon-background="#27c6a6"
      @click="$router.push('/orders/notifications')"
    />
    <message-list-card
      icon="warning"
      titleOn="反馈结果"
      titleTime="2021-05-05 18:18:18"
      :value="feedbackTitle"
      icon-background="#2474f1"
      @click="$router.push('/feedbacks')"
    />
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue"
import { NavBar, Toast } from "vant"
import MessageListCard from "./MessageListCard.vue"
import MessageService from "@/services/message"

export default defineComponent({
  name: "Message",
  components: {
    MessageListCard,
    [NavBar.name]: NavBar,
    [Toast.name]: Toast,
  },
  setup() {
    let feedbackTitle = ref<string>("")
    let orderTitle = ref<string>("")
    let noticeTitle = ref<string>("")

    function getMessage() {
      MessageService.getMessage().then(({ status, data, message }) => {
        if (status !== 200) {
          Toast(message)
          return
        }
        if (data.feedback?.question !== undefined) {
          feedbackTitle.value = data.feedback?.question
        }
        if (data.message?.title !== undefined) {
          noticeTitle.value = data.message?.title
        }
        if (data.orderNotice?.updatetime !== undefined) {
          orderTitle.value = data.orderNotice.updatetime
        }
      })
    }
    onMounted(getMessage)

    return { feedbackTitle, orderTitle, noticeTitle }
  },
})
</script>
